.services {
	position: relative;
	&__wrap {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		position: relative;
		font-family: 'Oswald', sans-serif;
		margin-top: 300px; }
	&__wrap h2 {
		font-size: 70px;
		width: 100%;
		text-align: center; }
	&__block {
		position: relative;
		margin-bottom: 20px;
		transition: 0.5s;
		opacity: 0; }
	&__block:hover {
		transform: scale(1.2); }
	&__block p {
		box-sizing: border-box;
		position: absolute;
		top: 10px;
		left: 0;
		padding: 10px;
		padding-bottom: 12px;
		padding-left: 30px;
		background: rgba(#000, 0.4);
		font-size: 18px;
		line-height: 22px;
		color: #fff; }
	&__wrap-video {
		display: block;
		position: absolute;
		top: -200px;
		left: calc(50% - 115px); }
	&__wrap-video img {
		border-radius: 50%;
		width: 230px;
		height: 230px; } }

#curved {
	position: absolute;
	top: -40px;
	left: 75px;
	transform: rotate(-15deg);
	font-size: 16px;
	color: #003399;
	text-transform: uppercase; }
#curved:hover {
	color: #cd3a0d; }

@media (min-width: 900px) {
	.services {
		margin-top: 120px;
		&__wrap {
			margin-top: 0;
			justify-content: space-between; }
		&__wrap h2 {
			position: absolute;
			width: auto;
			top: 90px;
			left: -240px;
			font-size: 145px;
			letter-spacing: 5px;
			color: rgba(#000, 0.2);
			transform: rotate(-90deg); }
		&__block p {
			width: 310px; }
		&__wrap-video {
			top: -950px;
			left: calc(75% - 115px);
			margin: 0; } } }

@media (min-width: 1200px) {
	.services {
		&__wrap-video {
			top: -700px;
			left: calc(73% - 115px); } } }

.animation-up {
	animation: animation-up 1s forwards; }

.animation-up:nth-child(3) {
	animation-delay: 0.5s; }
.animation-up:nth-child(4) {
	animation-delay: 1s; }
.animation-up:nth-child(5) {
	animation-delay: 1.5s; }
.animation-up:nth-child(6) {
	animation-delay: 2s; }
.animation-up:nth-child(7) {
	animation-delay: 2.5s; }

@keyframes animation-up {
	0% {
		opacity: 0;
		transform: translateY(200px); }
	100% {
		opacity: 1;
		transform: translateY(0); } }
