.advantages {
	position: absolute;
	z-index: 99;
	margin-right: 20px;
	&__wrap {
		padding: 20px;
		background: #fff; }
	&__list {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		list-style: none;
		padding: 0; }
	&__item {
		padding-left: 0px;
		margin-top: 10px;
		margin-bottom: 30px;
		opacity: 0; }
	&__item:first-child {
		padding-left: 70px;
		background: url(../img/icon-1.png) no-repeat left; }
	&__item:nth-child(2) {
		padding-left: 70px;
		background: url(../img/icon-2.png) no-repeat left; }
	&__item:last-child {
		padding-left: 70px;
		background: url(../img/icon-3.png) no-repeat left; }
	&__item h3 {
		margin: 0;
		margin-top: -8px;
		margin-bottom: 3px;
		font-size: 13px;
		font-weight: bold;
		padding-left: 0; }
	&__item p {
		margin: 0;
		font-size: 12px;
		color: #757575;
		line-height: 14px; } }

@media (min-width: 700px) {
	.advantages {
		&__list {
			display: flex;
			flex-direction: row; } } }
@media (min-width: 900px) {
	.advantages {
		&__item {
			padding-left: 80px;
			margin-top: 10px;
			width: 180px; }
		&__item:first-child {
			margin-left: 20px; }
		&__item:nth-child(2) {
			margin-left: 20px; }
		&__item:last-child {
			margin-left: 20px; } } }
@media (min-width: 1200px) {
	.advantages {
		&__item {
			width: auto; } } }

.animation-opacity {
	animation: animation-opacity 1s forwards; }
.animation-opacity:nth-child(2) {
	animation-delay: 0.5s; }
.animation-opacity:last-child {
	animation-delay: 1s; }


@keyframes animation-opacity {
	0% {
		opacity: 0; }
	100% {
		opacity: 1; } }
