.title {
	padding-top: 310px;
	& h3 {
		font-size: 24px;
		color: #1f2837;
		padding-left: 25px;
		font-weight: 300; }
	& h1 {
		margin-bottom: 50px;
		font-family: 'Oswald', sans-serif;
		font-size: 38px;
		line-height: 40px;
		letter-spacing: 1px;
		padding-left: 25px; }
	&__wrap {
		margin-top: 650px;
		padding-left: 25px;
		opacity: 0; }
	&__text {
		font-size: 18px;
		line-height: 23px; }
	&__image {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		width: auto;
		margin: 0 auto;
		margin-top: 120px;
		text-align: center;
		max-width: 580px; }
	&__image div {
		opacity: 0; }
	&__image img {
		margin-top: 20px;
		border: 1px solid #fff;
		box-shadow: 0px 0px 40px -10px #000; }
	&__image p {
		color: #fff;
		font-size: 16px;
		line-height: 20px; }
	&__title {
		position: relative;
		font-family: 'Oswald', sans-serif;
		font-size: 25px;
		font-weight: bold;
		line-height: 24px;
		letter-spacing: 1.5px;
		width: 220px; }
	&__title::after {
		content: '';
		position: absolute;
		bottom: -15px;
		left: 0;
		width: 100%;
		height: 40px;
		background: rgba(#d76a33, 0.3); } }

@media (min-width: 400px) {
	.title {
		&__wrap {
			margin-top: 460px; } } }

@media (min-width: 700px) {
	.title {
		& h1 {
			margin-bottom: 90px; }
		&__wrap {
			margin-top: 330px; } } }
@media (min-width: 900px) {
	.title {
		padding-top: 158px;
		& h1 {
			margin-bottom: 110px; }
		&__text {
			width: 400px; }
		&__title {
			font-size: 40px;
			line-height: 40px;
			position: absolute;
			top: 1000px;
			left: calc(70% - 115px);
			width: 330px;
			text-align: left; }
		&__image {
			width: 590px;
			align-items: flex-start;
			flex-wrap: wrap; }
		&__image div {
			width: 280px; }
		&__image div:last-child {
			width: auto; } } }

@media (min-width: 1200px) {
	.title {
		padding-top: 198px;
		& h3 {
			font-size: 25px; }
		& h1 {
			margin-bottom: 130px;
			font-size: 50px;
			line-height: 60px;
			letter-spacing: 1.5px; }
		&__text {
			width: 460px; }
		&__image {
			margin: 0;
			flex-direction: row; } } }

.animation-left {
	animation: animation-left 1s forwards; }

.animation-right {
	animation: animation-right 1s forwards; }

.animation-down {
	animation: animation-down 1s forwards; }

.animation-down:nth-child(3) {
	animation-delay: 0.5s; }
.animation-down:nth-child(4) {
	animation-delay: 1s; }
.animation-down:nth-child(5) {
	animation-delay: 1.5s; }
.animation-down:nth-child(6) {
	animation-delay: 2s; }

@keyframes animation-left {
	0% {
		opacity: 0;
		transform: translateX(-400px); }
	100% {
		opacity: 1;
		transform: translateX(0); } }

@keyframes animation-right {
	0% {
		opacity: 0;
		transform: translateX(400px); }
	100% {
		opacity: 1;
		transform: translateX(0); } }

@keyframes animation-down {
	0% {
		opacity: 0;
		transform: translateY(-200px); }
	100% {
		opacity: 1;
		transform: translateY(0); } }
