.wrapper {
	display: flex;
	background: linear-gradient(to bottom, #ededed 0%, #ededed 58%, #004b9a 58%, #004b9a 100%); }

@media (min-width: 700px) {
	.wrapper {
		background: linear-gradient(to bottom, #ededed 0%, #ededed 50%, #004b9a 50%, #004b9a 100%); } }

@media (min-width: 900px) {
	.wrapper {
		background: linear-gradient(to bottom, #ededed 0%, #ededed 40%, #004b9a 40%, #004b9a 100%); } }

@media (min-width: 1200px) {
	.wrapper {
		background: linear-gradient(to bottom, #ededed 0%, #ededed 50%, #004b9a 50%, #004b9a 100%); } }
