.about-work {
	display: none;
	background: #fff;
	width: 0%;
	height: 1922px;
	margin-left: 0%;
	&__dog {
		background: none; }
	&__blue-block {
		height: 770px;
		background: #004b9a;
		box-shadow: 10px 0px 100px #000; } }

@media (min-width: 900px) {
	.about-work {
		display: block;
		width: 44%;
		height: 1922px;
		margin-left: -44%;
		&__dog {
			position: absolute;
			top: 120px;
			right: 0%;
			width: 500px;
			height: 1000px;
			background: url(../img/dog.png) no-repeat;
			background-size: 90%; } } }

@media (min-width: 1200px) {
	.about-work {
		display: block;
		width: 44%;
		height: 1922px;
		&__dog {
			right: 5%;
			width: 550px;
			background-size: 100%; } } }
